import { Component, OnInit, OnDestroy } from '@angular/core';
import { interval } from 'rxjs';
import { tap } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import { DetailService } from 'src/app/mobile/shared/services/detail.service';
import { UserService } from 'src/app/mobile/shared/services/user.service';
import { NgxSmartModalService } from 'ngx-smart-modal';

@Component({
	selector: 'app-fancy2',
	templateUrl: './fancy2.component.html',
	styleUrls: ['./fancy2.component.css']
})
export class Fancy2Component implements OnInit, OnDestroy {

	public fancy2Data;
	public bookingData;
	public loader = false;
	public event_slug;
	public event_id;
	public betDelay;
	public selected = {
		fancy2Data: null,
		object: null,
		matchIndex: null,
		runnerIndex: null,
		type: null
	};
	public betDelaySub;
	public fancy2Sub;
	public formValue = {
		price: 1.01,
		size: 0
	};

	constructor(private aRoute: ActivatedRoute, private dService: DetailService, private uService: UserService, private ngxSmartModalService: NgxSmartModalService) { }

	ngOnInit() {

		this.event_slug = this.aRoute.snapshot.params.slug;
		this.event_id = this.aRoute.snapshot.params.event_id;
		this.betDelaySub = interval(10000).pipe(
			tap(() => {
				this.dService.getBetDelay({ slug: this.event_slug }).subscribe(resp => {
					if (resp.status && resp.data.length) {
						this.betDelay = resp.data[0];
					}
				});
			})
		).subscribe();
		this.fancy2Sub = interval(10000).pipe(
			tap(() => {
				this.dService.getFancy2Market(this.event_slug, this.event_id).subscribe(resp => {
					console.log('--------- fancy2 ---------', resp);
					if (resp.status && resp.data.items) {
						if (JSON.stringify(this.fancy2Data) !== JSON.stringify(resp.data.items)) {
							this.fancy2Data = resp.data.items;
						}
					}
				});
			})
		).subscribe();
	}

	openForm(fancy2Data, object, type, matchIndex, runnerIndex) {
		if (object.exchange[type].length) {
			this.formValue = {
				price: object.exchange[type][0].price,
				size: 0
			};
			this.selected = {
				fancy2Data, object, type, runnerIndex, matchIndex
			}
		} else {
			this.closeForm();
		}
	}

	closeForm() {
		this.formValue = {
			price: 1.01,
			size: 0
		};
		this.selected = {
			fancy2Data: null,
			object: null,
			matchIndex: null,
			runnerIndex: null,
			type: null
		};
	}

	changeSize(add, val) {
		if (val) {
			if (add) {
				this.formValue.size = +this.formValue.size + (val * 1000);
			} else if (this.formValue.size > 0) {
				const result = this.formValue.size - (val * 1000);
				this.formValue.size = result > -1 ? result : 0;
			}
		} else {
			this.formValue.size = 0;
		}
	}

	changePrice(add) {
		if (add) {
			this.formValue.price = this.formValue.price + 0.01;
		} else if (this.formValue.price > 1.01) {
			this.formValue.price = this.formValue.price - 0.01;
		}
	}

	makeBet() {
		const requestData = {
			'size': this.formValue.size,
			'price': this.formValue.price,
			'runner': this.selected.object.runnerName,
			'bet_type': this.selected.type,
			'session_type': 'match_odd',
			'market_id': this.selected.fancy2Data.marketId,
			'event_id': this.selected.fancy2Data.eventId,
			'sec_id': this.selected.object.selectionId,
			'rate': this.getLatestRate(),
			'market_name': this.selected.fancy2Data.title
		};
		this.dService.makeBet(this.event_slug, requestData).subscribe(resp => {
			console.log(resp);
		});
	}

	getLatestRate() {
		return this.fancy2Data[this.selected.matchIndex].runners[this.selected.runnerIndex].exchange[this.selected.type][0].price;
	}

	addRemoveFavorite(data) {
		const requestData = {
			'event_id': data.event_id,
			'market_id': data.market_id,
			'market_type': 'fancy2',
			'favorite': +data.is_favorite ? 'remove' : 'add'
		};
		this.uService.addRemoveFavorite(requestData).subscribe(resp => {
			if (resp.status) {
				data.is_favorite = !+data.is_favorite;
			}
		});
	}

	getBookingDetail(data) {
		this.bookingData = [];
		const requestData = {
			'event_id': data.event_id,
			'market_id': data.market_id,
			'session_type': 'fancy2',
			'price': '-'
		};
		this.dService.getBookingList(requestData).subscribe(resp => {
			if (resp.status) {
				this.bookingData = resp.data;
				this.ngxSmartModalService.getModal('bookDetailModal').open();
			}
		})
	}

	ngOnDestroy() {
		this.fancy2Sub.unsubscribe();
		this.betDelaySub.unsubscribe();
	}
}
