import { Component, OnInit, OnDestroy } from '@angular/core';
import { DetailService } from 'src/app/mobile/shared/services/detail.service';
import { ActivatedRoute } from '@angular/router';
import { FormGroup } from '@angular/forms';
import { interval } from 'rxjs';
import { tap } from 'rxjs/operators';

@Component({
	selector: 'app-match-odds',
	templateUrl: './match-odds.component.html',
	styleUrls: ['./match-odds.component.css'],
	providers: [DetailService]
})
export class MatchOddsComponent implements OnInit, OnDestroy {

	public matchOdds;
	public matchOddsNew;
	public loader = false;
	public event_slug;
	public event_id;
	public betDelay;
	public selected = {
		matchOdds: null,
		object: null,
		matchIndex: null,
		runnerIndex: null,
		type: null
	};
	public betDelaySub;
	public matchOddSub;
	public formValue = {
		price: 1.01,
		size: 0
	};

	public matchOddsForm: FormGroup;

	constructor(private aRoute: ActivatedRoute, private dService: DetailService) { }

	ngOnInit() {

		this.event_slug = this.aRoute.snapshot.params.slug;
		this.event_id = this.aRoute.snapshot.params.event_id;
		this.betDelaySub = interval(100000).pipe(
			tap(() => {
				this.dService.getBetDelay({ slug: this.event_slug }).subscribe(resp => {
					if (resp.status && resp.data.length) {
						this.betDelay = resp.data[0];
					}
				});
			})
		).subscribe();
		this.matchOddSub = interval(100000).pipe(
			tap(() => {
				this.dService.getMatchOdd(this.event_slug, this.event_id).subscribe(resp => {
					console.log(resp);
					if (resp.status && resp.data.items) {
						this.matchOdds = resp.data.items;
					}
				});
			})
		).subscribe();
	}

	openForm(matchOdds, object, type, matchIndex, runnerIndex) {
		if (object.exchange[type].length) {
			this.formValue = {
				price: object.exchange[type][0].price,
				size: 0
			};
			this.selected = {
				matchOdds, object, type, runnerIndex, matchIndex
			}
		} else {
			this.closeForm();
		}
	}

	closeForm() {
		this.formValue = {
			price: 1.01,
			size: 0
		};
		this.selected = {
			matchOdds: null,
			object: null,
			matchIndex: null,
			runnerIndex: null,
			type: null
		};
	}

	changeSize(add, val) {
		if (val) {
			if (add) {
				this.formValue.size = +this.formValue.size + (val * 1000);
			} else if (this.formValue.size > 0) {
				const result = this.formValue.size - (val * 1000);
				this.formValue.size = result > -1 ? result : 0;
			}
		} else {
			this.formValue.size = 0;
		}
	}

	changePrice(add) {
		if (add) {
			this.formValue.price = this.formValue.price + 0.01;
		} else if (this.formValue.price > 1.01) {
			this.formValue.price = this.formValue.price - 0.01;
		}
	}

	makeBet() {
		const requestData = {
			'size': this.formValue.size,
			'price': this.formValue.price,
			'runner': this.selected.object.runnerName,
			'bet_type': this.selected.type,
			'session_type': 'match_odd',
			'market_id': this.selected.matchOdds.marketId,
			'event_id': this.selected.matchOdds.eventId,
			'sec_id': this.selected.object.selectionId,
			'rate': this.getLatestRate(),
			'market_name': this.selected.matchOdds.title
		};
		if (this.betDelay) {
			setTimeout(() => {
				this.dService.makeBet(this.event_slug, requestData).subscribe(resp => {
					console.log(resp);
				});
			}, this.betDelay.bet_delay * 1000);
		} else {
			this.dService.makeBet(this.event_slug, requestData).subscribe(resp => {
				console.log(resp);
			});
		}
	}

	getLatestRate() {
		return this.matchOdds[this.selected.matchIndex].runners[this.selected.runnerIndex].exchange[this.selected.type][0].price;
	}

	ngOnDestroy() {
		this.matchOddSub.unsubscribe();
		this.betDelaySub.unsubscribe();
	}
}
