import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'millis' })
export class MillisPipe implements PipeTransform {
    transform(value: string): any {
        if (value.length === 13) {
            return +value;
        } else if (value.length) {
            return +value * 1000;
        }
    }
}