import { Component, OnInit } from '@angular/core';
import { DetailService } from '../../shared/services/detail.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-betting-detial-page',
  templateUrl: './betting-detial-page.component.html',
  styleUrls: ['./betting-detial-page.component.css'],
  providers: [DetailService]
})
export class BettingDetialPageComponent implements OnInit {

  public matchOdds2;
  public fancy;
  public fancy2;
  public lottery;

  constructor(private aRoute: ActivatedRoute, private dService: DetailService) { }

  ngOnInit() {
    const event_slug = this.aRoute.snapshot.params.slug;
    const event_id = this.aRoute.snapshot.params.event_id;

    this.dService.getBookmarket(event_slug, event_id).subscribe(resp => {
      if (resp.status && resp.data.items) {
        this.matchOdds2 = resp.data.items;
      }
      console.log('------- matchOdds2 --------', resp.data);
    });
    this.dService.getFancyMarket(event_slug, event_id).subscribe(resp => {
      if (resp.status && resp.data.items) {
        this.fancy = resp.data.items;
      }
      console.log('------- fancy --------', resp.data);
    });
    this.dService.getFancy2Market(event_slug, event_id).subscribe(resp => {
      if (resp.status && resp.data.items) {
        this.fancy2 = resp.data.items;
      }
      console.log('------- fancy2 --------', resp.data);
    });
    this.dService.getLottery(event_slug, event_id).subscribe(resp => {
      if (resp.status && resp.data.items) {
        this.lottery = resp.data.items;
      }
      console.log('------- lottery --------', resp.data);
    });
  }

}
