import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DetailService } from 'src/app/mobile/shared/services/detail.service';
import { interval } from 'rxjs';
import { tap } from 'rxjs/operators';

@Component({
	selector: 'app-fancy',
	templateUrl: './fancy.component.html',
	styleUrls: ['./fancy.component.css']
})
export class FancyComponent implements OnInit, OnDestroy {

	public fancyData;
	public loader = false;
	public event_slug;
	public event_id;
	public betDelay;
	public selected = {
		fancyData: null,
		object: null,
		matchIndex: null,
		runnerIndex: null,
		type: null
	};
	public betDelaySub;
	public fancySub;
	public formValue = {
		price: 1.01,
		size: 0
	};

	constructor(private aRoute: ActivatedRoute, private dService: DetailService) { }

	ngOnInit() {

		this.event_slug = this.aRoute.snapshot.params.slug;
		this.event_id = this.aRoute.snapshot.params.event_id;
		this.fancySub = interval(10000).pipe(
			tap(() => {
				this.dService.getFancyMarket(this.event_slug, this.event_id).subscribe(resp => {
					console.log('--------- fancy ---------', resp);
					if (resp.status && resp.data.items) {
						this.fancyData = resp.data.items;
					}
				});
			})
		).subscribe();
	}

	openForm(fancyData, object, type, matchIndex, runnerIndex) {
		if (object.exchange[type].length) {
			this.formValue = {
				price: object.exchange[type][0].price,
				size: 0
			};
			this.selected = {
				fancyData, object, type, runnerIndex, matchIndex
			}
		} else {
			this.closeForm();
		}
	}

	closeForm() {
		this.formValue = {
			price: 1.01,
			size: 0
		};
		this.selected = {
			fancyData: null,
			object: null,
			matchIndex: null,
			runnerIndex: null,
			type: null
		};
	}

	changeSize(add, val) {
		if (val) {
			if (add) {
				this.formValue.size = +this.formValue.size + (val * 1000);
			} else if (this.formValue.size > 0) {
				const result = this.formValue.size - (val * 1000);
				this.formValue.size = result > -1 ? result : 0;
			}
		} else {
			this.formValue.size = 0;
		}
	}

	changePrice(add) {
		if (add) {
			this.formValue.price = this.formValue.price + 0.01;
		} else if (this.formValue.price > 1.01) {
			this.formValue.price = this.formValue.price - 0.01;
		}
	}

	makeBet() {
		const requestData = {
			'size': this.formValue.size,
			'price': this.formValue.price,
			'runner': this.selected.object.runnerName,
			'bet_type': this.selected.type,
			'session_type': 'match_odd',
			'market_id': this.selected.fancyData.marketId,
			'event_id': this.selected.fancyData.eventId,
			'sec_id': this.selected.object.selectionId,
			'rate': this.getLatestRate(),
			'market_name': this.selected.fancyData.title
		};
		this.dService.makeBet(this.event_slug, requestData).subscribe(resp => {
			console.log(resp);
		});
	}

	getLatestRate() {
		return this.fancyData[this.selected.matchIndex].runners[this.selected.runnerIndex].exchange[this.selected.type][0].price;
	}

	ngOnDestroy() {
		this.fancySub.unsubscribe();
	}
}
