import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalService } from '../../shared/services/global.service';
import { UserService } from '../../shared/services/user.service';
import { interval } from 'rxjs';
import { tap } from 'rxjs/operators';

@Component({
    selector: 'mobile-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit, OnDestroy {

    public menuOpen = false;
    public balance;
    public commentary;
    private _headerSub;

    constructor(private router: Router, private gService: GlobalService, private uService: UserService) { }

    navigateTo(name) {
        this.router.navigate(['mobile/' + name]);
    }

    ngOnInit(): void {
        this._headerSub = interval(1000).pipe(
            tap(() => {
                this.gService.getBalance().subscribe(resp => {
                    if (resp.status) {
                        this.balance = resp.data;
                    }
                });
                this.gService.getCommentory().subscribe(resp => {
                    if (resp.status) {
                        this.commentary = resp.data;
                    }
                });
            })
        ).subscribe();
    }

    logout() {
        this.uService.logout();
    }

    ngOnDestroy() {
        this._headerSub.unsubscribe();
    }
}
